import PropTypes from 'prop-types'
import Button from './Button'
import { useLocation } from 'react-router'
const Header = (props) => {
    const location = useLocation()
    return (
        <header className="header">
            <h1 style={{ color: '#907FA4' }}>TASKY<span style={{ color: '#F38BA0' }}>.IO</span></h1>
            {location.pathname === '/' && <Button color={props.showForm ? '#F38BA0' : '#907FA4'} text={props.showForm ? 'Close':'Add'} onClick={() => props.setAddBtn(!(props.showForm))} />}
        </header>
    )
}


Header.defaultProps = {
    title: "TASK TRACKER",
}

Header.propTypes = {
    title: PropTypes.string,
}

export default Header
