import { useState } from "react"
const AddTask = ({onAdd}) => {
    const [text,setText]= useState('')
    const [day,setDate]= useState('')
    const [reminder,setReminder]= useState(false)
    

    const submitTask = (e) => {
        e.preventDefault()
        if(!text) {
            alert("please add the task name !")
            return
        }
        onAdd({text,day,reminder})

        setText('')
        setDate('')
        setReminder(false)

    }

    return (
        <form className="add-form" onSubmit={submitTask}>
            <div className='form-control' >
                <label>Task:</label>
                    <input type='text' value={text} placeholder='add a new task' onChange={(e) => setText(e.target.value)} />
                
            </div>
            <div className='form-control'>
                <label>Day and time: </label>
                    <input type='text' value={day} placeholder='add date and time'  onChange={(e) => setDate(e.target.value)}/>
               
            </div>
            <div className='form-control form-control-check  '>
                <label>Set reminder:</label>

                    <input type='checkbox' checked={reminder} onChange={(e) => setReminder(e.currentTarget.checked)}  />
            </div>
            <input type='submit' value='Add new task' className='btn btn-block' />
        </form>
    )
}

export default AddTask
